.logoAbout {
  width: 150px;
  height: 150px;
  border: 3px solid var(--bs-orange);
  background-color: black;
}

@media(min-width: 800px) {
  .logoAbout {
    width: 200px;
    height: 200px;
  }
}

@media(min-width: 1200px) {
  .logoAbout {
    width: 225px;
    height: 225px;
  }
}